:root {
    --primary-color: rgb(30, 58, 138);
    --primary-color-w500: rgb(59, 130, 246);
    --primary-color-w300: rgb(147, 197, 253);
    --primary-color-w300-transparent: rgba(147, 197, 253, 0.4);
    --green-color: rgb(34, 197, 94);
    --green-color-transparent: rgba(34, 197, 94, 0.6);
    --error-color: rgb(218, 75, 64);
    --error-color-transparent: rgba(218, 75, 64, 0.6);
    --disable-color: rgba(144, 168, 210, 0.2);
}


* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-size: 10px;
}

body {
    font-family: 'Josefin Sans', sans-serif;

}

input:focus,
select:focus,
textarea:focus,
button:focus {
    outline: none;
}

table {
    width: 100%;
    table-layout: fixed;
    border-collapse: collapse;
}

th,
td {
    padding: 1.2rem 1.6rem;
    border-bottom: 0.1rem solid var(--primary-color-w300-transparent);
}

th {
    background-color: var(--disable-color);
    color: var(--primary-color);
    font-size: 1.6rem;
}

td {
    font-size: 1.4rem;
    text-align: center;
}


table tr th:first-child {
    border-top-left-radius: 0.8rem;
}

table tr th:last-child {
    border-top-right-radius: 0.8rem;
}

.container {
    max-width: 1140px;
    margin: 0 auto;
}

.flex {
    display: flex;
}

.flex.flex-vertical {
    flex-direction: column;
}

.flex.flex-center {
    justify-content: center;
    align-items: center;
}

.grid {
    display: grid;
    column-gap: 2.4rem;
    width: 100%;
}

.grid.grid-2-cols {
    grid-template-columns: repeat(2, 1fr);
}


.grid.grid-3-cols {
    grid-template-columns: repeat(3, 1fr);
}

.grid.grid-4-cols {
    grid-template-columns: repeat(4, 1fr);
}

.grid.grid-7-cols {
    grid-template-columns: repeat(7, 1fr);
}

.spacer.spacer-height-sm {
    height: 0.8rem;
}

.spacer.spacer-height-md {
    height: 1.6rem;
}

.spacer.spacer-height-lg {
    height: 2.4rem;
}

.spacer.spacer-height-xl {
    height: 3.2rem;
}

.spacer.spacer-width-sm {
    display: inline-block;
    width: 0.8rem;
}

.spacer.spacer-width-md {
    display: inline-block;
    width: 1.6rem;
}

.spacer.spacer-width-lg {
    display: inline-block;
    width: 2.4rem;
}

.spacer.spacer-width-xl {
    display: inline-block;
    width: 3.2rem;
}

.italic {
    font-style: italic;
}

.bold {
    font-weight: 700;
}

.light {
    font-weight: 200;
}

.semi-bold {
    font-weight: 600;
}

.header-1 {
    font-size: 4.8rem;
}

.header-2 {
    font-size: 4.0rem;
}

.header-3 {
    font-size: 3.2rem;
}

.header-4 {
    font-size: 2.8rem;
}

.header-5 {
    font-size: 2.4rem;
}

.header-6 {
    font-size: 2rem;
}

.body-text-1 {
    font-size: 1.2rem;
}

.body-text-2 {
    font-size: 1.6rem;
}

.text-center {
    text-align: center;
    margin: auto 0;
}

.primary-color {
    color: var(--primary-color);
}

.list-row,
.table-title {
    display: grid;
}

.clickable {
    transition: all 0.2s;
}

.clickable:hover {
    background-color: var(--disable-color);
}

.svg-icon {
    width: 2.4rem;
    height: 2.4rem;
}

.svg-icon--white {
    color: white;
}

.svg-icon--black {
    color: black;
}

.image-placeholder {
    background-color: rgba(176, 202, 238, 0.3);
    border-radius: 0.8rem;
}

.ReactModal__Overlay {
    opacity: 0;
    transition: opacity 200ms ease-in-out;
}

.ReactModal__Overlay--after-open {
    opacity: 1;
}

.ReactModal__Overlay--before-close {
    opacity: 0;
}